<script>
import { nextTick, watch, onUpdated } from 'vue'
// import ArrowRight from './Icons/ArrowRight.vue'
// import ArrowDown from './Icons/ArrowDown.vue'
import StarRating from 'vue-star-rating'
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import ElButton from '../../../elements/ElButton.vue'

export default defineComponent({
  components: {
    // ArrowRight,
    // ArrowDown,
    StarRating,
    ElButton,
  },
  props: {
    node: {
      type: Object,
      required: true,
    },
    isRoot: {
      type: Boolean,
      default: false
    },
    indentSize: {
      type: Number,
      required: true,
    },
    gap: {
      type: Number,
      required: true,
    },
    getNode: {
      type: Function,
      required: true,
    },
    setNode: {
      type: Function,
      required: true,
    },
    updateNode: {
      type: Function,
      required: true,
    },
    expandRowByDefault: {
      type: Boolean,
      default: false,
    },
    useIcon:{
      type: Boolean,
      default: true,
    },
    rowHoverBackground: {
      type: String,
      default: '#e0e0e0',
    },
    expandable: {
      type: Boolean,
      default: true,
    }
  },
  data: ()=>({
    maxProgressWidth: 266
  }),
  emits: ['emitNodeExpanded', 'emitOnUpdated'],
  setup(props, { emit }) {
    // // console.log(props)
    const toggleExpanded = node => {
      if (props.node.nodes == null || props.node.nodes.length == 0) {return}
      if (props.expandable) {
        props.node.expanded = props.node.nodes ? !props.node.expanded : false
        nextTick(() => {
          emit('emitNodeExpanded', node, props.node.expanded)
        })
      }
    }

    watch(() => props.expandRowByDefault, newVal => {
      // if (props.node.nodes == null || props.node.nodes.length == 0) {return}
      if (props.node.nodes) {
        props.node.expanded = !props.expandable
      }
    }, {
      immediate: true,
    })

    // redirect the event toward the Tree component
    const emitNodeExpanded = (node, state) => {
      emit('emitNodeExpanded', node, state)
    }

    const emitOnUpdated = (node, state) => {
      emit('emitOnUpdated', node, state)
    }

    onUpdated(() => {
      emitOnUpdated()
    })

    return {
      toggleExpanded,
      emitNodeExpanded,
      emitOnUpdated
    }
  },
  methods: {
    copy(value) {
            function fallbackCopyTextToClipboard(text) {
                var textArea = document.createElement("textarea");
                textArea.value = text;
                
                // Avoid scrolling to bottom
                textArea.style.top = "0";
                textArea.style.left = "0";
                textArea.style.position = "fixed";

                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();
                var successful;
                try {
                    successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    // console.log('Fallback: Copying text command was ' + msg);
                } catch (err) {
                    console.error('Fallback: Oops, unable to copy', err);
                }

                document.body.removeChild(textArea);
                return successful;
            }
            if (value) {
                const clipboardData =
                // event.clipboardData ||
                window.clipboardData ||
                // event.originalEvent?.clipboardData ||
                navigator.clipboard;
                try {
                    clipboardData.writeText(value); 
                    this.$message(localizeFilter('copied')) 
                } catch (e) {
                    if(fallbackCopyTextToClipboard(value)){
                        this.$message(localizeFilter('copied')) 
                    }
                }
            }
      },
    contactReferral(userId) {
      this.$emit('contactReferral', userId)
    },
    getStatus(stars) {
      return localizeFilter('marketing_status') + ' ' + stars
    },
    async sellReferral(node) {
     const data = JSON.parse(JSON.stringify(node))
     await this.$store.dispatch('sellReferral', {data})
    }
  }
})
</script>

